import React, { lazy } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { updateShowMint } from './state/collection/reducer'
const Mint = lazy(() => import('./widgets/Mint'))

const MintView = ({ accessKey }) => {
  const { selectedCollectionId, showMint } = useSelector((state) => state.collection)
  const dispatch = useDispatch()

  const toggleMint = () => {
    dispatch(
      updateShowMint({
        showMint: false,
      }),
    )
  }
  return (
    <Mint
      mintOpen={showMint}
      onClose={toggleMint}
      collectionId={selectedCollectionId}
      accessKey={accessKey}
      collection={{}}
    />
  )
}

export default MintView
